pre {
  display: inline-block;
  text-align: left;
  margin: 0;
}

html, body, #root {
  height: 100%;
}
html, body, input {
  font-family: monospace;
}

.wrapper {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.header {
  display: -ms-flexbox;
  display: flex;
}
.header pre {
  margin: auto;
}
.header img {
  margin: 5px;
}

.background {
  text-align: center;
}

.game {
  background-color: #222;
  height: 100%;
  padding: 20px;
  color: white;
}

.gamePre {
  white-space: pre;
  text-align: left;
  display: inline-block;
}
.gamePre.shake {
  -webkit-animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
          animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

@-webkit-keyframes shake {
  10%, 90% { -webkit-transform: translate3d(-2px, 2px, 0); transform: translate3d(-2px, 2px, 0); }
  20%, 80% { -webkit-transform: translate3d(3px, -3px, 0); transform: translate3d(3px, -3px, 0); }
  30%, 50%, 70% { -webkit-transform: translate3d(-6px, -6px, 0); transform: translate3d(-6px, -6px, 0); }
  40%, 60% { -webkit-transform: translate3d(6px, -6px, 0); transform: translate3d(6px, -6px, 0); }
}

@keyframes shake {
  10%, 90% { -webkit-transform: translate3d(-2px, 2px, 0); transform: translate3d(-2px, 2px, 0); }
  20%, 80% { -webkit-transform: translate3d(3px, -3px, 0); transform: translate3d(3px, -3px, 0); }
  30%, 50%, 70% { -webkit-transform: translate3d(-6px, -6px, 0); transform: translate3d(-6px, -6px, 0); }
  40%, 60% { -webkit-transform: translate3d(6px, -6px, 0); transform: translate3d(6px, -6px, 0); }
}

.joyride {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}



